@import url(//db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
@import url(https://fonts.googleapis.com/css2?family=Finger+Paint&family=Macondo&display=swap);

body {
  margin: 0;
  padding: 0;
}
.game {
  -webkit-text-stroke: 2px #2a324b;
  font-family: 'Pokemon', 'Macondo', cursive;
  color: #ef3e2e;
  letter-spacing: 4px;
  background-image: linear-gradient(to right bottom, #ef3e2e, #f66929, #f98d2d, #fbad3c, #fbcc54, #ffbf5d, #ffb368, #fba872, #da7c7b, #a25e7d, #62496d, #2a324b);  z-index: 2;
  min-height: 100vh;
  text-align: center;
}
.heder {
  -webkit-text-stroke: 4px #2a324b;
  font-size: 10rem;
  position: absolute;
  left: 10%;
  right: 10%;
  z-index: 1;
  top: 15%;
  max-width: 100vw;
  max-height: 100vh;
  display: block;

  animation: textZoom 3.5s ease-in-out;
}

@keyframes textZoom {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(0);
  }
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.1rem;
  gap: 0.1rem;
}

.game-data {
  display: flex;
  justify-content: space-evenly;
  font-size: 4vh;
  background-color: #c7ccdb;
}

@media screen and (min-width: 901px) and (max-width: 1280px) {
  .heder {
    font-size: 7rem;
    top: 30%;
  }
  .game-data {
    font-size: 1.8rem;
    justify-content: space-around;
    margin-bottom: 1.3rem;
  }
}

@media screen and (min-width: 601px) and (max-width: 899px) {
  .heder {
    font-size:4rem;
    top: 40%;
  }
  .game-data {
    font-size: 1.5rem;
    justify-content: space-around;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 599px) {
  .heder {
    font-size: 4rem;
    top: 20%;
  }
  .game-data {
    font-size: 1rem;
    justify-content: space-around;
  }
}

.card {
  position: relative;
  margin: 1rem auto;
  justify-content: space-around;
 
}
.card img {
  width: 100%;
  max-width: -webkit-fit-content ;
  max-width: -moz-fit-content ;
  max-width: fit-content ;
  height: auto 0;
  display: block;
  border: 3px dotted #240606;
  border-radius: 10px;
}

.card .front {
  transform: rotateY(90deg);
  transform: all ease-in-out 0.3s;
  position: absolute;
  margin: 0 auto;
  border: transparent;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.3s;
}

.card .back {
  transition: all ease-in-out 0.3s;
  transition-delay: 0.3s;

}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;

}

